'use strict';

document.addEventListener("DOMContentLoaded", function(event) {
    const ProjectPage = {
        id: window.location.href.split('/')[window.location.href.split('/').length - 1],
        title: document.querySelector('.project__h1'),
        location: document.querySelector('.project__h3'),
        desc: document.querySelector('.project__p'),
        breadcrumb: document.querySelector('.project__page--active'),
        imgList: document.querySelector('.project__imgList'),
        
        setData: function() {
            let self = this;

            function get(url) {
                return new Promise(function(resolve, reject) {
                    let req = new XMLHttpRequest();
                    req.open('GET', url);
                    req.onload = function() {
                        if (req.status == 200) {
                            resolve(req.response);
                        } else {
                            window.location.href = '/404';
                            reject(Error(req.statusText));
                        }
                    }

                    req.onerror = function() {
                        reject(Error('Network Error'));
                    }

                    req.send();
                });
            }

            function getJSON(url) {
                return get(url).then(JSON.parse);
            }

            // Projects List
            getJSON('https://apiprod.denisglazkov.best/project/list').then(function(data) {
                let projectList = {};

                for (let i = 0; i < data.data.length; i++) {
                    if (data.data[i].attributes.is_published) {
                        projectList[i] = {
                            id: data.data[i].id,
                            priority: data.data[i].attributes.priority
                        };
                    }
                }

                return projectList;
                
            }).then(function(data) {
                let projectID = window.location.href.split('/')[window.location.href.split('/').length - 1];
                let sortProjects = [];

                for (let prop in data) {
                    sortProjects.push([data[prop].priority, data[prop]]);
                }

                sortProjects.sort(function(a, b) {
                    return a[0] - b[0];
                });

                let firstProject = sortProjects[0][1].priority;
                let lastProject = sortProjects[sortProjects.length - 1][1].priority;
                let prevPage = document.querySelector('.project__previous a');
                let nextPage = document.querySelector('.project__next a');

                for (let i = 0; i < sortProjects.length; i++) {
                    if (sortProjects[i][1].id == projectID) {
                        
                        if (sortProjects[i][1].priority == firstProject) {
                            prevPage.remove();
                        } else {
                            prevPage.href = `/project/${sortProjects[i - 1][1].id}`;
                        }
                        
                        if (sortProjects[i][1].priority == lastProject) {
                            nextPage.remove();
                        } else {
                            nextPage.href = `/project/${sortProjects[i + 1][1].id}`;
                        }
                    }
                }
            });

            getJSON(`https://apiprod.denisglazkov.best/project/${self.id}`).then(function(data) {
                return data;
            }).then(function(data) {
                let imagesID = [];

                self.title.innerText = data.data.attributes.title;
                self.breadcrumb.innerText = data.data.attributes.title;
                self.location.innerText = data.data.attributes.location;
                self.desc.innerText = data.data.attributes.description;

                for (let i = 0; i < data.included.length; i++) {
                    imagesID.push(data.included[i].id);

                    if (data.included[i].attributes.orientation) {

                        if (self.imgList.lastChild.classList.contains('project__portrait')) {
                            let firstImgURI = `https://apiprod.denisglazkov.best/static${data.included[i].attributes.uri}`;
                            let firstImg  = `<a data-fancybox="gallery" style="background: url(${firstImgURI}) no-repeat; background-size: cover; background-position: center;" href="${firstImgURI}"><img src="${firstImgURI}"></img></a>`;
                            self.imgList.lastChild.insertAdjacentHTML('beforeEnd', firstImg);
                        } else {
                            let imgPortrait = `<div class="project__portrait"></div>`;
                            self.imgList.insertAdjacentHTML('beforeEnd', imgPortrait);

                            let firstImgURI = `https://apiprod.denisglazkov.best/static${data.included[i].attributes.uri}`;
                            let firstImg  = `<a data-fancybox="gallery" style="background: url(${firstImgURI}) no-repeat; background-size: cover; background-position: center;" href="${firstImgURI}"><img src="${firstImgURI}"></img></a>`;
                            self.imgList.lastChild.insertAdjacentHTML('beforeEnd', firstImg);
                        }

                    } else {
                        let imgURI = `https://apiprod.denisglazkov.best/static${data.included[i].attributes.uri}`;
                        let imgLandscape = `
                            <div class="project__landscape">
                                <a data-fancybox="gallery" href="${imgURI}"><img src="${imgURI}" width="100%"></a>
                            </div>`;

                        self.imgList.insertAdjacentHTML('beforeEnd', imgLandscape);
                    }
                }

                return imagesID;
            }).then(function(imagesID) {
                let tagsID = [];

                function getTags() {
                    return new Promise((resolve, reject) => {
                        for (let i = 0; i < imagesID.length; i++) {
                            getJSON(`https://apiprod.denisglazkov.best/image/${imagesID[i]}`).then(function(image) {
                                for (let i = 0; i < (image.data.relationships.length - 1); i++) {
                                    tagsID.push(image.data.relationships[i].id);
                                }
                            });        
                        }

                        let timerID = setInterval(function() {
                            if (tagsID.length >= imagesID.length) {
                                resolve([ ...new Set(tagsID) ]);
                                clearInterval(timerID);
                            }
                        }, 100);
                    });
                }

                getTags().then(function(filterTags) {
                    let tagsList = document.querySelector('.project__filter');
                    
                    for (let i = 0; i < filterTags.length; i++) {
                        getJSON(`https://apiprod.denisglazkov.best/tag/${filterTags[i]}`).then(function(tag) {
                            let li = `
                            <li>
                                <span class="projects__black-border"></span>
                                <a href="/tag/${tag.data.id}">${tag.data.attributes.title}</a>
                            </li>`;
                            tagsList.insertAdjacentHTML('beforeEnd', li);
                        });
                    }
                });
            });
        },
        init: function() {
            this.setData();
        }
    }
    
    ProjectPage.init();
});